* 
{
    margin: 0;
    padding: 0;
    text-decoration: none;
    list-style: none;
}
:root
{
    --nav-height : 80px;
    --main-height : calc();
    --main-color : #FF9F00;
    --second-color : #303030;
}
body
{
    background-color: rgb(0, 0, 0);
}

body::before
{
    content: '';
    position: absolute;
    top: -38%;
    left: -45%;
    width: 100%;
    height: 100%;
    background: linear-gradient(#FF9F00,#F10C0C);
    clip-path: circle(8% );
    z-index: -2;
}

/* Header : our nav bar propreties*/
#header
{
    margin: auto;
    margin-top: 10px;
    width: 90vw;
    height: var(--nav-height);
    max-height: max-content;
    display: block;
    background-color: #303030;
    box-shadow: 0px 20px 20px rgba(0, 0, 0, 0.5);
    background: rgba(255, 255, 255, 0.1);
    border-radius: 15px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
    border-right: 1px solid rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(20px);
}

/*container of all header components*/
.header_container
{
    width: 80vw;
    height: 100%;
    display: block;
    margin: 0 auto;
}

/* Logo and logo manipulation*/
.logo_container{
    height: 100%;
    display: table;
    float: left;
}
.logo_container h1{
    text-transform: uppercase;
    color: white;
    height: 100%;
    display: table-cell;
    vertical-align: middle;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size: 3em;
    /* Shadow and shadow animation*/
    /*text-shadow: 0 0 2px #faa823;*/
    transition: .5s color ;
}
/*shadow animation for the logo*/
#header:hover .logo_container h1
{
    color: var(--main-color);
    /* text-shadow: 0 5px 8px rgb(240, 240, 240); */
}

/* Navigation options*/
.navigation
{
    float: right;
    height: 100%;
}
.navigation a
{
    height: 100%;
    display: table;
    float: left;
    padding: 0 20px;
    color: white;
}
.navigation a li 
{
    display: table-cell;
    vertical-align: middle;
    font-family: monospace;
    font-weight: bolder;
    font-size: 1.4em;
    color: inherit;
}
.navigation a:hover{
    color: var(--main-color);
    background: #202020;
}


/* Burger and responsive*/
#burger
{
    display: none;
}
.menu{
    float: right;
    height: 100%;
    display: flex;
}
.menu img
{
    align-self: center;
    height: 35%;
    cursor: pointer;
    display: none;
}

/* Code shower*/
.container 
{
    width: 80vw;
    height: 91vh;
    /*display: grid;*/
    display: none;
}
.container .code
{
    width: 90%;
    padding: 5px;
    justify-self: center;
}

/* Responsive Nav
@media (max-width : 900px) {
    .header_container
    {
        width: 98%;
    }
}*/

@media (max-width : 950px) {
    .menu img 
    {
        display: block;
    }
    .navigation
    {
        display: grid;
        position: fixed;
        top: calc(var(--nav-height) + 15px);;
        left: -150vw;
        background: rgba(255, 255, 255, 0.1);        
        width: 100vw;
        height: calc(100vh - var(--nav-height) * 3);
        padding-bottom: var(--nav-height);
        padding-top: var(--nav-height);
        border-radius: 15px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.5);
        border-right: 1px solid rgba(255, 255, 255, 0.5);
        backdrop-filter: blur(20px);
        align-items: center;
        transition: 0.6s left ease-out;
    }
    .navigation a
    {
        font-size: 2.5em;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: 0.4s color;
        border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    }
    .navigation a:hover{
        background: none;
    }
    #burger:checked ~ .navigation
    {
        left: 0;
    }
    
}






